.profile_info_wrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px ;
}

.avatar {
    width: 220px;
    height: 280px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    border: 1px solid #8c8c8c;
    border-radius: 4px;
    background-color: #ffffff;
    box-shadow:         3px 4px 7px 0px rgba(50, 50, 50, 0.38);
}

.avatar__wrapper {
    text-align: center;
    background-color: #ffffff;
}

.avatar__wrapper__img {
    width: 200px;
    background-color: #ffffff;
    margin: 0 auto;

}

.avatar__wrapper__button {
    width: 200px;
    height: 30px;
    margin: 0 auto;
    cursor: pointer;
    border: none;
    outline: none;
    background-color: #8C8C8C;
    border-radius: 3px;
    color: #ffffff;
}

.info {
    background-color:  #ffffff;
    height: 280px;
    width: 70%;
    box-shadow:         3px 4px 7px 0px rgba(50, 50, 50, 0.38);
    border: 1px solid #8c8c8c;
    border-radius: 4px;
    padding: 20px;
}

.info__title {
    font-size: 25px;
    font-weight: bold;
}

.info__status {
    font-size: 11px;
    font-style: italic;
}