.header {
  grid-area: h;
  /* background: #F8CD02; */
  background-color: #8C8C8C;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 60px;
  position: fixed;
  width: 100%;
  z-index: 2;
}

.header  > div {
  height: 100%;
}

.nav {
  display: flex;
  justify-content: space-between;
  width: 70%;
}

.activeLink {
  border-bottom: 4px solid #FFDD00;
}

a:hover {
  border-bottom: 4px solid #FFDD00;
}

nav a {
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 600;
  color: #fff;
  padding-bottom: 16px;
}

.loginBlock {
  display: flex;
  align-items: center;
  color: #fff;
  text-shadow: 1px 1px 2px rgba(255, 221, 0, 1);  font-style: italic;
}

.userPhoto {
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 50%;
  margin: 10px;
  

}

.logout {
  cursor: pointer;
}