 .postsBlock {
   width: 70%;
   float: right;
 }

 .posts {
   background-color: transparent;
   display: flex;
   flex-direction: column-reverse;
 }

 .button {
   width: 200px;
   height: 30px;
   margin: 0 auto;
   cursor: pointer;
   border: none;
   outline: none;
   background-color: #8C8C8C;
   border-radius: 3px;
   color: #ffffff;
 }

 .addPost {
   border: 1px solid #8c8c8c;
   border-radius: 4px;
   background-color: #ffffff;
   box-shadow: 3px 4px 7px 0px rgba(50, 50, 50, 0.38);
   padding: 10px;
   height: 150px;
   margin-bottom: 20px;
 }

 .addPost textarea {
   width: 100%;
   resize: none;
   height: 95px;
   margin-bottom: 3px;
   padding: 10px;
   font-size: 15px;
 }

 .addPost button {
   float: right;
 }