* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #EDEEF0;
}

.app-wrapper {
  display: grid;
  grid-template-rows: 50px auto;
  grid-template-columns: 2fr 10fr;
  grid-gap: 0px;
  grid-template-areas: "h h" "n c";
}

.app-wrapper_content {
  grid-area: c;
  position: relative;
  margin-right: 50px;
  padding-top: 25px;
}
