.post {
  border: 1px solid #8c8c8c;
  border-radius: 4px;
  background-color: #ffffff;
  box-shadow:         3px 4px 7px 0px rgba(50, 50, 50, 0.38);
  margin-bottom: 10px;
  padding: 10px;
  position: relative;
}
.id__wrapper {
  color: grey;
  font-size: 10px;
}

.heart__wrapper {
  position: absolute;
  bottom: 5px;
  right: 5px;

}
.heartCount {
  font-size: 13px;
  font-style: italic;
}