.formControl {
    position: relative;
}

div.formControl.error input,
div.formControl.error textarea  {
    border: 1px solid red ;
    
}

div.formControl.error span  {
    color: red;
    position: absolute;
    left: 0;
    bottom: -25px;
 
}

.formSummaryError {
    border: 1px solid red;
    width: 50px;
    text-align: center;
}