.dialogs_wrapper {
   background-color: #f6f6f6;
   display: grid;
   grid-template-columns: 2fr 10fr;

  }

  .dialogs_items {
   padding: 10px;
   border: 1px dashed cyan;
  }

.dialogs_items .active {
   color: gold;
}

  .dialog {
   color: white;
   border: 1px dashed gold;
  }

  .messages {
   padding: 10px;
   border: 1px dashed cyan;
  }

  .message {
   border: 1px dashed gold;
  }