.userPhoto {
    width: 100px;
    border-radius: 50%;
    margin: 0 auto;
    display: block;

}
.users_wrapper {
    display: flex;
    justify-content: space-around;
    flex-direction: row;
    align-items: center;
    border: 1px solid #8c8c8c;
    border-radius: 4px;
    background-color: #ffffff;
    box-shadow:         3px 4px 7px 0px rgba(50, 50, 50, 0.38);
}
.users_wrapper {
    width: 550px;
    margin: 5px;
    padding: 5px 10px;
}
 .right_column {
     display: flex;
     width: 350px;
     justify-content: space-between;
} 

.title {
    font-size: 20px;
    font-weight: bold;
}

.location {
    width: 100px;
    text-align: left;
}

.selectedPage {
    font-weight: bold;
}

.current {
    overflow: scroll;
    width: 100px;


}

.current span {
    margin: 0 5px;
    cursor: pointer;
    overflow: hidden;

}

.button {
    width: 135px;
    height: 30px;
    margin: 0 auto;
    cursor: pointer;
    border: none;
    outline: none;
    background-color: #8C8C8C;
    border-radius: 3px;
    color: #ffffff;
}

.button:disabled {
    background-color:  #CACACA;
    cursor: not-allowed;
}

